<template>
  <div class="three"></div>
</template>
<script>
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
import { CubemapGenerator } from "three/examples/jsm/loaders/EquirectangularToCubeGenerator.js";
export default {
  name: "three",
  data() {
    return {
      scene: null, // 
      camera: null, // 
      renderer: null, // 
      controls: null, // 
      width: null, // 
      height: null, // 
      container: null, // dom
      mouse: null, // 
      rayList: null, // 
      LineGroup: null, //  
      AmbientLight: null, // 
      gridHelper: null, // 
      axisHelper: null, // 
      lowIndex: 4, // 
      sphereMesh: null,
      isUserInteracting: false,
      onMouseDownMouseX: 0,
      onMouseDownMouseY: 0,
      lon: 0,
      onMouseDownLon: 0,
      lat: 0,
      onMouseDownLat: 0,
      phi: 0,
      theta: 0
    };
  },
  methods: {
    init() {
      this.initRenderer();
      this.initScene();
      this.initCamera();
      this.initLight();
      this.initHelper();
      this.initControls();

      this.initSphere();

      window.addEventListener('resize', this.onWindowResize, true)
    },
    // 
    animate() {
      requestAnimationFrame(() => {
        this.animate();
      });
      this.controls.update()
      // this.update()
      this.renderer.render(this.scene, this.camera);
    },
    initSphere() {
      var geometry = new THREE.SphereBufferGeometry(500, 60, 40);
      // invert the geometry on the x-axis so that all of the faces point inward
      geometry.scale(-1, 1, 1);
      var texture = new THREE.TextureLoader().load(
        "./static/image/.jpg"
      );
      var material = new THREE.MeshBasicMaterial({ map: texture });
      this.sphereMesh = new THREE.Mesh(geometry, material);
      this.scene.add(this.sphereMesh);

      // document.addEventListener("mousedown", this.onPointerStart, false);
      // document.addEventListener("mousemove", this.onPointerMove, false);
      // document.addEventListener("mouseup", this.onPointerUp, false);
    },
    onPointerStart(event) {
      this.isUserInteracting = true;

      var clientX = event.clientX || event.touches[0].clientX;
      var clientY = event.clientY || event.touches[0].clientY;

      this.onMouseDownMouseX = clientX;
      this.onMouseDownMouseY = clientY;

      this.onMouseDownLon = this.lon;
      this.onMouseDownLat = this.lat;
    },
    onPointerMove(event) {
      if (this.isUserInteracting === true) {
        var clientX = event.clientX || event.touches[0].clientX;
        var clientY = event.clientY || event.touches[0].clientY;

        this.lon =
          (this.onMouseDownMouseX - clientX) * 0.1 + this.onMouseDownLon;
        this.lat =
          (clientY - this.onMouseDownMouseY) * 0.1 + this.onMouseDownLat;
      }
    },
    onPointerUp() {
      this.isUserInteracting = false;
    },
    onWindowResize(){
      this.width = document.querySelector(".three").offsetWidth
      this.height = document.querySelector(".three").offsetHeight
      this.camera.aspect = this.width / this.height
      this.camera.updateProjectionMatrix()
      this.renderer.setSize(this.width, this.height)
    },
    // 
    initScene() {
      this.scene = new THREE.Scene();
      this.scene.background = new THREE.Color(0x000000); // 
    },
    update() {
      if (this.isUserInteracting === false) {
        this.lon += 0.1;
      }

      this.lat = Math.max(-85, Math.min(85, this.lat));
      this.phi = THREE.Math.degToRad(90 - this.lat);
      this.theta = THREE.Math.degToRad(this.lon);

      this.camera.target.x = 500 * Math.sin(this.phi) * Math.cos(this.theta);
      this.camera.target.y = 500 * Math.cos(this.phi);
      this.camera.target.z = 500 * Math.sin(this.phi) * Math.sin(this.theta);

      this.camera.lookAt(this.camera.target);
    },
    // 
    initCamera() {
      this.camera = new THREE.PerspectiveCamera(
        70,
        this.width / this.height,
        1,
        10000
      );
      this.camera.target = new THREE.Vector3(0, 0, 0);
      this.camera.position.set(100, 0, 0);
    },
    // 
    initRenderer() {
      this.renderer = new THREE.WebGLRenderer({
        antialias: true, // 
        precision: "lowp", //   "highp", "mediump"  "lowp"
        alpha: true,
        powerPreference: "high-performance"
      });
      // 
      this.renderer.setPixelRatio(window.devicePixelRatio);
      this.renderer.setSize(this.width, this.height); // 
      this.renderer.sortObjects = false; // true.
      this.container.appendChild(this.renderer.domElement);
    },
    // 
    initLight() {
      this.AmbientLight = new THREE.AmbientLight(0xffffff, 5);
      this.scene.add(this.AmbientLight);
    },
    // 
    initControls() {
      this.controls = new OrbitControls(this.camera, this.container);
      this.controls.screenSpacePanning = true;
      this.controls.autoRotate = true; // 
      this.controls.autoRotateSpeed = -0.5; // 
      this.controls.maxDistance = 450 //
      this.controls.minDistance = 150 //
      this.controls.saveState(); // 
      this.controls.addEventListener("change", () => {
      });
    },
    // 
    initHelper() {
      this.gridHelper = new THREE.GridHelper(4000, 100, 0x000a52, 0x000a52);
      // this.scene.add(this.gridHelper)
      this.axisHelper = new THREE.AxisHelper(250);
      this.scene.add(this.axisHelper);
    }
  },
  mounted() {
    let dom = document.querySelector(".three");
    if (dom) {
      this.container = dom;
      this.width = dom.offsetWidth;
      this.height = dom.offsetHeight;
      this.init();
      this.animate();
    }
  },
  created() {}
};
</script>
<style scoped>
.three,
.iframe {
  height: 100%;
  width: 100%;
}
</style>



